/**
 * 三角箭头
 */
.rs-post__header {
  @apply mb-4;
}
@screen lg {
  .rs-post__header {
    @apply mb-8;
  }
}
.rs-post__title {
  @apply text-3xl leading-normal;
  color: #383838;
}
.rs-post__meta {
  @apply flex;
  color: #383838;
}
.rs-post__meta > div {
  @apply mr-2;
}
.site__banner .rs-post__title {
  color: #fff;
}
.site__banner .rs-post__meta {
  color: #fff;
}

.text-center .rs-post__meta {
  @apply justify-center;
}

.text-right .rs-post__meta {
  @apply justify-end;
}

/* =Comments
-------------------------------------------------------------- */
.comments-title {
  @apply mt-12 mb-8 text-base leading-normal font-normal;
}

.content__comments {
  @apply mb-10 ml-0 pl-0;
}
.content__comments .avatar {
  @apply rounded-full border border-gray-300;
}
.content__comments ol.comment-list {
  @apply list-none;
}
.content__comments ol.comment-list .pingback {
  @apply leading-relaxed mb-6;
}
.content__comments .children {
  @apply ml-0 pl-10 bg-no-repeat;
  background-position: left 20px;
}
.content__comments li.comment > div img.avatar {
  @apply absolute;
  left: 29px;
  top: 29px;
}

.comment-respond {
  @apply text-sm;
}

.comment-form p {
  @apply w-full;
}
.comment-form p.logged-in-as {
  @apply mb-6;
}
.comment-form p .form-allowed-tags {
  @apply m-0 text-xs leading-loose;
  color: #5e5e5e;
}
.comment-form label {
  @apply block leading-none;
}
.comment-form input[type=text],
.comment-form input[type=email],
.comment-form input[type=url],
.comment-form textarea {
  @apply w-full;
}
.comment-form input[type=submit] {
  transform: scale(1);
  transition: all 0.2s ease;
  text-align: center;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  cursor: pointer;
  font-weight: 400;
  display: inline-block;
  line-height: 1.5;
  border-radius: 3px;
  padding: calc(0.5em - 1px) 1em;
  background-color: #f3f3f3;
  border-width: 1px;
  border-color: #ddd;
  color: #383838;
}
.comment-form input[type=submit]:hover {
  background-color: #ebebeb;
}
.comment-form input[type=submit]:active {
  background-color: #e4e4e4;
}
.comment-form input[type=submit]:hover {
  border-color: #dadada;
}
.comment-form input[type=submit]:active {
  border-color: #d3d3d3;
}
.comment-form-cookies-consent {
  @apply flex w-full items-center text-sm text-gray-600;
}
@screen lg {
  .comment-form-author, .comment-form-email, .comment-form-url {
    @apply w-1/3 float-left;
  }
}

.comment-respond {
  @apply mt-12;
}
.comment-respond #wp-comment-cookies-consent {
  @apply mr-2;
}
.comment-respond .comment-form-cookies-consent {
  @apply flex items-center;
}
.comment-respond .comment-form-cookies-consent label {
  margin-bottom: 0;
}

.required {
  color: red;
}

.comment {
  @apply text-sm;
}
.comment .comment-body {
  @apply relative border border-gray-300 bg-gray-100;
  padding: 25px 25px 25px 98px;
  margin: 2em 0 0;
}
.comment-author .avatar {
  position: absolute;
  left: 25px;
  top: 29px;
}
.comment-author .says {
  padding-left: 2px;
}
.comment-author b {
  @apply font-normal text-gray-600;
}
.comment-metadata {
  line-height: 16px;
  margin-bottom: 10px;
}
.comment-metadata a {
  @apply text-xs text-gray-500;
}
.comment-reply-link {
  @apply inline-block mt-2 text-gray-500;
}
.comment-reply-title {
  @apply mt-4 font-medium text-lg;
}
.comment-notes {
  @apply text-gray-500;
}
.comment-content {
  @apply mt-3 text-gray-800;
}
.comment-navigation {
  @apply overflow-hidden mt-8;
}